@import "variable";
@import "utils";

.brand {
  display: flex;
  justify-content: center;
  position: relative;
  max-width: 1100px;
  margin: 0 auto;

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 380px;
    margin-right: 150px;
    @media screen and (max-width: $big-breakpoint) {
      width: 300px;
    }
    @media screen and (max-width: $breakpoint) {
      margin: auto;
    }
  }
  &__article {
    @include article;
    @media screen and (max-width: $breakpoint) {
      text-align: center;
    }
  }
  &__description {
    margin-top: 30px;
  }

  &__description:nth-child(2) {
    @media screen and (max-width: $breakpoint) {
      display: none;
    }
  }

  &__photo {
    overflow: hidden;
    width: 500px;
    height: 529px;
    transform: translateX(-108px);
    @media screen and (max-width: $breakpoint) {
      width: 40vw;
      height: 40vw;
      transform: translateX(0);

    }
    @media screen and (max-width: $one-picture-breakpoint) {
      display: none;
    }
  }

  &__photo-little {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 130px;
    height: 439px;
    overflow: hidden;
    @media screen and (max-width: $big-breakpoint) {
      display: none;
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    transition: $hover-duration;
    &:hover {
      transform: scale(1.2);
    }
  }

  &__aside {
    position: absolute;
    transform: rotate(-90deg);
    left: -135px;
    top: 100px;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    color: rgba(52, 79, 16, 0.5);
    font-size: 14px;
    line-height: 130%;
  }
}
